










































































































































































import { Vue, Component } from 'vue-property-decorator';
import userStore from '@/stores/user';
import ListChild from '../HomePage/components/ListChild.vue';

const dealList = [
  {
    currency: 'EOS',
    dealSize: 3333,
    price: 0.0023,
    statu: 1,
    Percentage: 10,
    id: 1,
  },
  {
    currency: 'EOS',
    dealSize: 3333,
    price: 0.0023,
    statu: 0,
    Percentage: 10,
    id: 2,
  },
  {
    currency: 'EOS',
    dealSize: 3333,
    price: 0.0023,
    statu: 2,
    Percentage: 10,
    id: 3,
  },
];

const growList = [
  {
    currency: 'EOS',
    dealSize: 3333,
    price: 0.0023,
    statu: 1,
    Percentage: 10,
    id: 1,
  },
  {
    currency: 'EOS',
    dealSize: 3333,
    price: 0.0023,
    statu: 0,
    Percentage: 10,
    id: 2,
  },
];

@Component({
  components: {
    ListChild,
  },
})
export default class extends Vue {
  isOptional = '2';
  dealList = [];
  growList = [];
  typeTableState = '1';
  upOrDown = '0';

  created() {
    this.setDealList(dealList);
    this.setGrowList(growList);
    userStore.setCurrency('2');
  }

  setDealList(list: any) {
    this.dealList = list;
  }

  setGrowList(list: any) {
    this.growList = list;
  }

  modifyGrowList(value: any) {
    this.isOptional = value;
  }

  onTypeTable(id: any) {
    this.typeTableState = id;
    if (this.upOrDown === '0' || this.upOrDown === '2') {
      this.upOrDown = '1';
    } else if (this.upOrDown === '1') {
      this.upOrDown = '2';
    }
    //获取数据时 根据this.isOptional结合this.typeTableState来判断应该调取的借口
  }
}
